.portfolio_tm_modalbox {
  .additional_images {
    width: 100%;
    height: auto;
    clear: both;
    float: left;
  }
  .additional_images ul {
    margin: 0px 0px 0px -30px;
    list-style-type: none;
    li {
      margin: 30px 0px 0 0px;
      float: left;
      width: 50%;
      padding-left: 30px;
      @media screen and (max-width: 767px) {
        width: 100%;
      }

      &:nth-child(3n-2) {
        width: 100%;
        img {
          width: 100%;
        }
        .my_image {
          height: 408px;
          @media screen and (max-width: 767px) {
            height: 165px;
          }
        }
      }
      .list_inner {
        width: 100%;
        height: auto;
        clear: both;
        float: left;
        position: relative;
      }
      .my_image {
        position: relative;
        height: 320px;
        @media screen and (max-width: 767px) {
          height: 165px;
        }
        img {
          opacity: 0;
          min-width: 100%;
        }
        .main {
          position: absolute;
          top: 0px;
          bottom: 0px;
          left: 0px;
          right: 0px;
          background-repeat: no-repeat;
          background-size: cover;
          background-position: center;
        }
      }
    }
  }
}

.modal-video-close-btn {
  cursor: pointer !important;
}

// portfolio animation
.lm_portfolio {
  .mobile_title {
    width: 100%;
    height: auto;
    clear: both;
    float: left;
    background: rgba(255, 255, 255, 0.6);
    position: absolute;
    z-index: 2;
    bottom: 0;
    padding: 10px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    cursor: default;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;

    h3 {
      font-size: 18px;
      font-weight: 600;
      color: #000;
    }
    span {
      font-family: "Poppins";
      font-weight: 500;
      color: #000;
    }
  }
}

.lm_portfolio .portfolio_filter ul li .inner:hover .mobile_title {
  -webkit-transform: translateY(0);
  transform: translateY(0);
}


